body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 18rem;
  margin: 10px auto;
}
.game{

}

.game-stat-button{
	width: 40%;
	margin: 10px;
}
.question-card {
	width: 100%;
}

.question{
	font-size: 50px;
	font-weight: bold;
	text-align: center;
}

.choice{
	margin: 5px;
}

.choice-button{
	width:100%;
	font-size:20px;
}
.timer-button{
	width: 90%;
	margin: 10px;
}

